@media screen and (min-width: 800px) {
  #App {
    width: 75%;
    margin: auto;
  }
}

@media screen and (min-width: 1200px) {
  #App {
    width: 50%;
    margin: auto;
  }
}

#App {
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

#current-number {
  color: white;
  font-size: 1.5em;
  background-color: #00BCD4;
  border-radius: 15px;
  padding: 3% 0;
  margin: 5% 0;
}


